import React, { createRef } from "react";
import { PlusOutlined } from "@ant-design/icons";
import i18n from "../../i18n";
import {
  getRequest,
  postRequest,
  putRequest,
} from "../../helpers/superagentAPI";

import {
  Modal,
  Button,
  Form,
  Input,
  Row,
  Col,
  Divider,
  Image,
  InputNumber,
} from "antd";
import toBase64 from "../../helpers/toBase64";

export default class DBModel extends React.Component {
  constructor(props) {
    super(props);
    this.initialState = () => ({
      id: props.id,
      modelName: props.modelName,
      modelTitle: props.title,
      reloadGrid: props.reloadGrid,
      buttonIcon: props.buttonIcon,
      model: null,
      isModalVisible: false,
      imagePreview: "",
      selectImageFile: null,
    });
    this.state = this.initialState();
    this.loadModel = async () => {
      if (this.state.id) {
        let data = await getRequest(`${this.state.modelName}/${this.state.id}`);
        this.setState(
          {
            model: data,
            imagePreview: `${process.env.REACT_APP_API_URL}${data.image}`,
          },
          () => {
            if (this.modelFormRef.current) {
              this.modelFormRef.current.setFieldsValue({
                rate: data.rate,
                companyNameEn: data.companyNameEn,
                companyNameAr: data.companyNameAr,
                descriptionEn: data.descriptionEn,
                descriptionAr: data.descriptionAr,
              });
            }
          }
        );
      }
    };

    this.imageFileChanged = () => {
      let files = document.getElementById("imageInput").files;
      this.setState(
        {
          selectImageFile: files.length != 0 ? files[0] : null,
        },
        async () => {
          const { selectImageFile } = this.state;
          if (selectImageFile == null) {
            this.setState({
              imagePreview: "",
            });
          } else {
            let imageBase64 = await toBase64(selectImageFile);
            this.setState({
              imagePreview: imageBase64,
            });
          }
        }
      );
    };

    this.onFormFinished = async (values) => {
      const { id, modelName, reloadGrid } = this.state;
      const {
        rate,
        companyNameEn,
        companyNameAr,
        descriptionEn,
        descriptionAr,
      } = values;
      let formData = new FormData();
      formData.append("rate", rate);
      formData.append("companyNameEn", companyNameEn);
      formData.append("companyNameAr", companyNameAr);
      formData.append("descriptionEn", descriptionEn);
      formData.append("descriptionAr", descriptionAr);
      if (this.state.selectImageFile)
        formData.append("image", this.state.selectImageFile);
      if (id) formData.append("id", id);
      await postRequest(modelName, formData);
      if (reloadGrid) reloadGrid();
      this.closeModal();
    };

    this.showModal = () => {
      this.setState(
        {
          isModalVisible: true,
          imagePreview: null,
        },
        async () => {
          if (this.state.id) await this.loadModel();
        }
      );
    };

    this.closeModal = () => {
      this.setState({
        isModalVisible: false,
      });
    };

    this.modelFormRef = createRef();
  }

  render() {
    const { modelTitle, imagePreview, buttonIcon, id } = this.state;
    return (
      <div style={{ width: "100%" }}>
        <Button
          type="primary"
          onClick={this.showModal}
          icon={buttonIcon}
          shape="round"
          className="btn_hover darkBlueBg"
          style={{ width: "100%" }}
        ></Button>
        {this.state.isModalVisible && (
          <Modal
            title={modelTitle}
            visible={this.state.isModalVisible}
            onCancel={this.closeModal}
            width={"85%"}
            okButtonProps={{
              form: "modelForm",
              htmlType: "submit",
              className: "darkBlueBg",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
          >
            <Form
              id={"modelForm"}
              ref={this.modelFormRef}
              onFinish={this.onFormFinished}
              style={{ marginTop: "5%" }}
            >
              <Row gutter={10}>
                <Row>
                  <Col span={24}>
                    {imagePreview != "" && (
                      <>
                        <Image src={imagePreview} />
                        <Divider />
                      </>
                    )}
                    <Form.Item
                      name={"image"}
                      label={i18n.t("Image")}
                      rules={[
                        {
                          required: !id,
                          message: i18n.t("Input is required"),
                        },
                      ]}
                    >
                      <Input
                        type={"file"}
                        accept={"image/*"}
                        id="imageInput"
                        ref={this.websiteImageFileRef}
                        onChange={this.imageFileChanged}
                      />
                    </Form.Item>
                  </Col>
                </Row>
                <Col span={24}>
                  <Form.Item
                    name={"companyNameEn"}
                    label={i18n.t("company Name English")}
                    rules={[
                      {
                        required: true,
                        message: i18n.t("input is required"),
                      },
                    ]}
                  >
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    name={"companyNameAr"}
                    label={i18n.t("company Name Arabic")}
                    rules={[
                      {
                        required: true,
                        message: i18n.t("input is required"),
                      },
                    ]}
                  >
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    name={"descriptionEn"}
                    label={i18n.t("Company Description English")}
                    rules={[
                      {
                        required: true,
                        message: i18n.t("input is required"),
                      },
                    ]}
                  >
                    <Input.TextArea />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    name={"descriptionAr"}
                    label={i18n.t("Company Description Arabic")}
                    rules={[
                      {
                        required: true,
                        message: i18n.t("input is required"),
                      },
                    ]}
                  >
                    <Input.TextArea />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    name={"rate"}
                    label={i18n.t("Rate")}
                    rules={[
                      {
                        required: true,

                        message: i18n.t("input is required"),
                      },
                    ]}
                  >
                    <InputNumber />
                  </Form.Item>
                </Col>
              </Row>
            </Form>
          </Modal>
        )}
      </div>
    );
  }
}
